import React from "react";
import { yazilar } from "../../content/yazilar";

export default function Yazilar() {
  return (
    <main style={{ maxWidth: 900, margin: '0 auto', padding: '2rem 1rem' }}>
      <h1 style={{ fontSize: '2rem', marginBottom: '1.5rem' }}>Yazılar</h1>
      {yazilar.map((yazi) => (
        <div key={yazi.slug} style={{ display: 'flex', flexDirection: 'row', gap: '2rem', alignItems: 'flex-start', border: '1px solid #eee', borderRadius: 12, padding: 24, marginBottom: 32 }}>
          <img src={yazi.image} alt={yazi.title} style={{ width: 180, height: 120, objectFit: 'cover', borderRadius: 8, boxShadow: '0 2px 8px #0001' }} />
          <div style={{ flex: 1 }}>
            <h2 style={{ margin: 0 }}>{yazi.title}</h2>
            <div style={{ color: '#888', fontSize: 14, marginBottom: 8 }}>{yazi.date}</div>
            <p style={{ marginBottom: 16 }}>{yazi.description}</p>
            <ul style={{ padding: 0, listStyle: 'none', margin: 0 }}>
              {yazi.files.map(file => (
                <li key={file.url} style={{ marginBottom: 6 }}>
                  <a href={file.url} download style={{ color: '#1976d2', textDecoration: 'underline' }}>{file.name}</a>
                  <span style={{ color: '#888', marginLeft: 8, fontSize: 13 }}>({file.size})</span>
                </li>
              ))}
            </ul>
          </div>
        </div>
      ))}
    </main>
  );
} 